import React, { Component } from 'react';
import './index.scss';
import '../../../style/style.scss';
import { Toast, Button } from 'zzcDesign';

let isClose = false;

export default class App extends Component {

    constructor( props ) {
        super( props );
    }

    markInfo() {
        Toast.info( 'markinfo', 2, () => {
            console.log( '' );
        }, false );
    }

    loadingInfo() {
        if ( isClose ) {
            isClose = false;
            Toast.hideToast();
        } else { 
            isClose = true;
            Toast.info( 'toast', 0, () => {},false );
        }
    }

    info() {
        Toast.info( '', 2, () => {
            console.log( '' );
        } );
    }

    success() {
        Toast.success( '', 2 );
    }

    error() {
        Toast.error( '', 2);
    }

    waring() {
        Toast.waring( '', 2 );
    }

    loading() {
        Toast.loading( '...', 2 );
    }

    render() {

        return (
            <div className="zzc-demo">
                <div className="zzc-demo-header">
                    <h1 className="zzc-demo-title">Toast </h1>
                </div>
                <div className="zzc-demo-body">
                    <Button onClick={this.markInfo}>no mark info</Button>
                    <Button onClick={this.info} type="main">info</Button>
                    <Button onClick={this.success} type="main">success</Button>
                    <Button onClick={this.error} type="sub">error</Button>
                    <Button onClick={this.waring} type="sub">waring</Button>
                    <Button onClick={this.loading} type="main">loading</Button>
                    <Button onClick={this.loadingInfo} type="main">toast</Button>
                </div>
            </div>

        );
    }
}


